import { getAllVideos } from "@/api/apiEndpoints";
import { useEffect, useState } from "react";

export default function HomePage() {
  const [videos, setVideos] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  useEffect(() => {
    const getAllVideosData = async () => {
      try {
        setLoading(true);
        const response = await getAllVideos();
        if (response.length === 0) {
          setError("No videos found");
          return;
        }
        setVideos(response);
        console.log(response);
      } catch (error) {
        setError(error.message);
      } finally {
        setLoading(false);
      }
    };
    getAllVideosData();
  }, []);
  return (
    <div className="flex justify-center items-center min-h-screen">
      <h1 className="text-2xl text-white">This is the home page</h1>
      {loading && <p className="text-white text-lg">Loading...</p>}
      {error && <p className="text-lg text-red-500">{error}</p>}
      <div className="">
        <ul className="list-style-none">
          {videos.map((video) => (
            <li key={video._id}>{video.title}</li>
          ))}
        </ul>
      </div>
    </div>
  );
}
